<template>
    <div>
        <div style="text-align:center;">
            <h3>录入楼房信息</h3>
        </div>
        <el-form :model="form" label-width="150px">
            <el-form-item prop="name" label="楼房名" >
                <el-input v-model="form.name" placeholder="请输入楼房名"></el-input>
            </el-form-item>
            <el-form-item prop="address"  label="地址">
                <el-input v-model="form.address" placeholder="请输入楼房地址"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="addBuilding">立即创建</el-button>
                <el-button @click="$router.go(-1)">取消</el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
import { addBuilding } from '@/api/building'
export default {
    name: "InsertRenter",
    data() {
        return {
            form: {
                name: '',
                address: ''
            }
        }
    },
    methods: {
        addBuilding() {
            addBuilding(this.form.name, this.form.address).then(() => {
                this.$message.success("提交成功")
                this.$router.go(-1)
            })
        }
    },
    created() {


    }
}
</script>

<style scoped>
.el-form {
    margin-top: 20px;
    padding: 0 50px;
}

.el-input {
    width: 24%;
}
</style>